/**
 *  Validate login-form
 *
 *  @author Jimmy
 */

export const validateLogin = (data) => {
  if (checkIfEmpty(data.userName)) {
    return 'Anvndarnamnet mste vara ifyllt!'
  }
  if (checkIfEmpty(data.password)) {
    return 'Lsenordet mste vara ifyllt!'
  }

  return false
}

/**
 *  Validate register-form-form
 *
 *  @author Jimmy
 */

export const validateRegister = (data) => {
  if (checkIfEmpty(data.userName)) {
    return 'Anvndarnamnet mste vara ifyllt!'
  }
  if (checkIfEmpty(data.email)) {
    return 'Mailadressen mste vara ifylld!'
  }
  if (checkIfEmpty(data.password)) {
    return 'Lsenordet mste vara ifyllt!'
  }
  if (checkIfEmpty(data.passwordConfirm)) {
    return 'Lsenordet mste vara ifyllt!'
  }
  if (checkIfEmpty(data.firstName)) {
    return 'Frnamnet mste vara ifyllt!'
  }
  if (checkIfEmpty(data.surname)) {
    return 'Efternamnet mste vara ifyllt!'
  }
  if (data.password !== data.passwordConfirm) {
    return 'Lsenorden r inte lika!'
  }

  return false
}

const checkIfEmpty = (data) => {
  if (data === '') {
    return true
  }
}



